<template>
    <div>
    <el-card class="box-card">
      <span></span>
    </el-card>
    <el-row>
      <el-col :span="24">
        <el-form ref="form" :model="query" label-width="95px">
          <el-form-item label="" style="width: 18%">
            <el-input v-model="query.key" placeholder="" @change="sreatech" clearable @keyup.native.enter="sreatech"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="query.type" placeholder="" @change="sreatech" clearable @keyup.native.enter="sreatech">
            <el-option v-for="(imet,index) in list" :key="index" :label="imet.name" :value="imet.id"></el-option>
            </el-select>
        </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="sreatech"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
    <el-row class="butt">
      <el-button type="primary" @click="add" ></el-button>
      <el-button type="danger" @click="del"></el-button>
    </el-row>

    <!--  -->
          <Table
          :loading="loading"
          :tableData="tableData"
          :tableCouml="tableCouml"
          @idshow="idshow">

          <template #title="data">
            <a @click="deladd(data.data)" href="javascript:void(0);">{{data.data.title}}</a>
          </template>

          <template #imgage="data">
            <el-image
            style="width: 100px; height: 100px"
            :src="img+data.data.picture"
            fit="fill"></el-image>
          </template>

          <template #state="data">
              <el-switch
                v-model="data.data.state"
                :active-value=1
                :inactive-value=0
                @change="changeserage(data.data)"
                active-color="#13ce66"
                inactive-color="#ff4949">
              </el-switch>
          </template>

          <template #option="data">
            <el-button size="mini" type="success" @click="mod(data.data)"></el-button>
            <el-button size="mini" type="danger" @click="handleDelete(data.data)"></el-button>
          </template>
    </Table>
      <!--  -->
          <Paging :query="query" :total="total" @paging="paging"></Paging>
        <!--  -->
        <Newsadd :isNewsadd="isNewsadd" @change="change" :Newsdata="Newsdata"></Newsadd>
        <!--  -->
        <Newstian :dialogVisible="dialogVisible" @change="change" :form="form" :title="title"></Newstian>
  </div>
</template>
<script>

import Table from "@/components/commts/Table.vue";
import Paging from "@/components/commts/Paging.vue";
import Newsadd from '@/components/News/Newsadd.vue';
import Newstian from '@/components/News/Newstian.vue'

export default {
          data() {
    return {
      form:{
        state:1,
        id: 0,
        hits: 0,
        },
      loading: true,
      list:[],
      query:{
        page:1,
        psize:5
      },
      tableData: [],
      tableCouml: [
                { prop: "id", label: "ID",},
                { prop: "typename", label: "",},
                { prop: "title", label: "", isshow:true,showname: "title"},
                { prop: "picture", label: "", isshow:true,showname: "imgage"},
                { prop: "state", label: "",  isshow:true,showname: "state"},
                { prop: "addtime", label: "",},
                { prop: "edituser", label: "",},
                { label: "", isshow: true, showname: "option" },
            ],
      total:0,
      img:'http://estate.eshareedu.cn/estate/upload/',
      lt:[],
      isNewsadd:false,
      dialogVisible: false,
      Newsdata:{},
      ids:[],
      modcom:{},
      title:'',
    };
  },
  components:{
    Paging,
    Table,
    Newsadd,
    Newstian
  },
  created(){
    this.getimet()
    this.gettypename()
  },
  methods:{
    // 
    add(){
      this.dialogVisible = true
      this.form = {state:1,id: 0,hits: 0,}
      this.title=''
    },
    // 
    mod(val){
      this.form = val
      // console.log(val)
      this.dialogVisible = true
      this.title=''
    },
    // 
    sreatech(){
      this.getimet()
    },
    // 
    deladd(data){
      // console.log(data)
      this.isNewsadd = true
      this.Newsdata = data
    },
    change(val){
      this.isNewsadd = val
      this.dialogVisible = val
      this.getimet()
      this.modcom = {}
    },
    // 
    async changeserage(data){
      // console.log(data)
      let res = await this.http.post('/news/add',data)
      if(res.errCode == 10000){
        this.$message.success('')
      }
    },
    // 
        paging() {
            this.getimet();
        },
    // 
    async gettypename(){
        let res = await this.http.get('/newstype/list')
        // console.log(res)
        if(res.errCode == '10000'){
            this.list = res.data.list
        }
    },
    // 
    idshow(val){
      this.ids.push(val)
    },
    async del(){
      let res = await this.http.post('/news/deleteall',{ids:this.ids})
      if(res.errCode == 10000){
        this.$message.success('')
        this.getimet()
      }
    },
    // 
     handleDelete(val){
       this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(async () => {
            let res = await this.http.get('/news/delete',{id:val.id})
            if(res.errCode == '10000'){
                this.$message.success('')
                this.getimet()
            }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });          
        });
    },
      // 
      async getimet(){
        let res = await this.http.get('/news/list',this.query)
        // console.log(res)
        if(res.errCode == '10000'){
          this.tableData = res.data.list
          this.total = res.data.counts
          this.loading = false
        }
      },
  },
}
</script>

<style lang="less" scoped>
@import '../assets/css/admin.css';
a{
  text-decoration: underline;
}
/deep/.el-icon-plus{
  border: 1px solid #ccc !important;
}
</style>