<template>
    <el-dialog
    :title="title"
    :visible.sync="dialogVisible"
    width="65%"
    center="center"
    :before-close="handleClose">
    <el-form ref="form" :model="form" label-width="95px">
        <el-form-item label="" style="width:50%;">
            <el-input v-model="form.title"></el-input>
        </el-form-item>
        <el-form-item label="">
            <editor-vue 
            ref="editor"
            :content="content"
            :readOnlys="readOnlys"
            @changeData="hChangeData"
            />
        </el-form-item>
        <el-form-item label="">
            <el-upload
                class="avatar-uploader"
                action="http://estate.eshareedu.cn/estate/api/upload/add"
                :headers="headers"
                :show-file-list="false"
                :on-success="handleAvatarSuccess"
                :before-upload="beforeAvatarUpload">
                <img v-if="imageUrl" :src="imageUrl" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
        </el-form-item>
        <el-form-item label="">
                <el-radio v-model="form.state" :label="1"></el-radio>
                <el-radio v-model="form.state" :label="0"></el-radio>
        </el-form-item>
        <el-form-item label="">
            <el-select v-model="form.type" placeholder="">
                <el-option v-for="(imet,index) in type" :key="index" :label="imet.name" :value="imet.id"></el-option>
            </el-select>
        </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
        <el-button @click="serate"> </el-button>
        <el-button type="primary" @click="submit"> </el-button>
    </span>
    </el-dialog>
</template>
<script>
import EditorVue from '../News/editorVue.vue'
export default {
    props:{
        dialogVisible:{
            type:Boolean,
            default:false
        },
        form:{
            type:Object,
            default:()=>{}
        },
        title:{
            type:String,
            default:''
        }
    },
    data(){
        return{
            headers:{Authorization:sessionStorage.getItem('token')},
                  
            query:{
                page:1,
                psize:5
            },
            type:[],
            imageUrl: '',
            readOnlys: false,
            content: ``,
            img:"http://estate.eshareedu.cn/estate/upload/"
        }
    },
    components:{
        EditorVue
    },
    created(){
        this.gettype()
        // this.getnews()
    },

    // updated(){
    //     this.title = this.modcom.id?'':''
    //     this.form = this.modcom?this.modcom:{}
        
    //     if(this.title == ''){
    //         this.imageUrl = "http://estate.eshareedu.cn/estate/upload/"
    //     }else{
    //         return this.imageUrl
    //     }    this.title==''?imageUrl+this.form.picture:
        
    // },

    methods:{
        // // 
        // async getnews(){
        //     let res = await this.http.get('/news/get',{id:this.form.id})
        //     // console.log(res)
        //     if(res.errCode == 10000){
        //         console.log(this.$refs)
        //     }
        // },

        // 
        async submit(){
            
            // this.form.state = this.form.state == ''?1:0
            this.form.content = this.content
            this.form.edituser = this.$store.state.username
            this.form.addtime = new Date().toLocaleString()
            // console.log(this.form)
            let res = await this.http.post('/news/add',this.form)
            // console.log(res)
            if(res.errCode == 10000){
                this.$message.success('')
                this.$emit('change',false)
                this.imageUrl = ""
                this.$refs.editor.html = ""
            }
        },
        // 
        hChangeData(editDataHtml){
        // html
        this.content = editDataHtml
        // this.content = this.content.replace(/<[/!][^<>]>/gi, '')
        console.log(this.content)
        },
        hUploadImg(file,insertFn){
        console.log(file)
        // url,conteng
        let imgUrl = 'https://img1.baidu.com/it/u=608404415,1639465523&fm=253&fmt=auto&app=120&f=JPEG?w=1280&h=800'
        insertFn(imgUrl);

        // 
        this.readOnlys = true;      
        },
        // 
        handleAvatarSuccess(res, file) {
            this.form.picture = res.data
            this.imageUrl = URL.createObjectURL(file.raw);
        },
        beforeAvatarUpload(file) {
            const isJPG = file.type === 'image/jpeg';
            const isLt2M = file.size / 1024 / 1024 < 2;

            if (!isJPG) {
            this.$message.error(' JPG !');
            }
            if (!isLt2M) {
            this.$message.error(' 2MB!');
            }
            return isJPG && isLt2M;
        },
        // 
        async gettype(){
            let res = await this.http.get('/newstype/list',this.query)
            // console.log(res)
            if(res.errCode == 10000){
                this.type = res.data.list
            }
        },
        // 
        serate(){
            this.$emit('change',false)
                this.$refs.editor.html = ""
                this.imageUrl = ""
        },
        // 
        handleClose(done) {
        this.$confirm('')
          .then(() => {
            // done();
            this.$emit('change',false)
                this.$refs.editor.html = ""
                this.imageUrl = ""
          })
          .catch(() => {});
      },
        async getdetail(id){
            let res = await this.http.get('/news/get',{id:id})
            this.$refs.editor.html = res.data.content
        }
    },
    watch:{
        form:{
            handler(newval){
                // console.log(newval)
                this.$nextTick(()=>{
                    if(newval.id){
                        this.imageUrl = this.img + newval.picture
                        this.getdetail(newval.id)
                    }
                })
                
            },
            deep:true
        }
    }
}
</script>

<style lang="less" scoped>
.el-form{
    display: block;
    /deep/.el-input__inner{
        border: 1px solid #ccc !important;
    }
}
.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>